.App{
  /* width: 100%;
  height: 100vh;
  background: url(./img/Login\ Desktop.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;; */
  text-align: center;
}

.Main-container{
  width: 100%;
  height: 100vh;
  background: url(./img/Desktop\ LowHD.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.Login-container{
  margin: auto;
  display: flex;
  justify-content: center;
  width: 50%;
}

.Login-Main{
  /* width: 100%;
  height: 100vh; */
  /* margin-top: 12rem; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 1.5rem;
  /* border: solid red 10px; */
  
}

.School-Logo{
  margin-top: 25px;
  width: 60%;
  height: 20vh;
}

.Login-Main .Main-title{
  color: #004383;
}

.Login-Main input{
  display: inline-block;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  padding: 10px 20px;
  border: 1px solid #b7b7b7;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  font: italic normal normal 16px/normal "Times New Roman", Times, serif;
  color: #004383;
  -o-text-overflow: clip;
  text-overflow: clip;
  background: rgba(252,252,252,1);
  -webkit-box-shadow: 2px 2px 2px 0 rgba(0,0,0,0.2) inset;
  box-shadow: 2px 2px 2px 0 rgba(0,0,0,0.2) inset;
  text-shadow: 1px 1px 0 rgba(255,255,255,0.66) ;
  -webkit-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
  -moz-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
  -o-transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
  transition: all 200ms cubic-bezier(0.42, 0, 0.58, 1);
}

.Login-btn{
  -webkit-border-radius: 4;
  -moz-border-radius: 4;
  border-radius: 4px;
  font-family: Arial;
  color: #ffffff;
  font-size: 20px;
  background: #004383;
  padding: 5px 20px 5px 20px;
  text-decoration: none;
  border-color: #004383;
  }
  
.Login-btn:hover {
    background: #07304b;
    text-decoration: none;
}

.Login-Main a{
  text-decoration: none;
}

/* Media query for all devices */

/* For Mobile Portrait View */
@media screen and (max-device-width: 640px)
and (orientation: portrait) {
  .Main-container{
    background: url(./img/Mobile\ Horizontal.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;;
  }

  .Login-Main{
    /* margin-top: 8rem; */
    row-gap: 1rem;
  }

  .School-Logo{
    margin-top: 25px;
    width: 50%;
    height: 15vh;
  }

  .Login-btn{
    font-size: 16px;
    padding: 10px 20px 10px 20px;
  }

  .Login-btn:hover {
    background: #004383;
    text-decoration: none;
  }
}

/* For Mobile Landscape View */
@media screen and (max-device-width: 840px)
and (orientation: landscape) {
  .Main-container{
    background: url(./img/Tab\ Landscape.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;;
  }
  .Login-Main{
    margin-top: 0;
    row-gap: 1rem;
  }

  .Login-btn{
    font-size: 14px;
    padding: 5px 40px 5px 40px;
  }

  .Login-Main input{
    width: 60%;
  }

  .link-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }  
}

/* For Tablet View */
@media screen and (min-device-width: 768px)
and (max-device-width: 1024px) {
  .Main-container{
    background: url(./img/Tab\ Landscape.png);
    background-repeat: no-repeat;
    background-size: 100% 95%;;
  }

  .Login-Main{
    /* margin-top: 20rem; */
  }

  /* .Login-btn{
    font-size: 16px;
    padding: 10px 20px 10px 20px;
  } */

  .Login-btn:hover {
    background: #004383;
    text-decoration: none;
  }
  
}